<template>
	<view class="content">
		<button type="default" @click="getData">getData</button>
		<button type="default" @click="setStorage">setStorage</button>
		<button type="default" @click="getStorage">getStorage</button>
		<button type="default" @click="removeStorage">removeStorage</button>
		<button type="default" @click="uploadPic">上传图片</button>
		<view >
			<image v-for="item in imgSrc" :src="item" mode="aspectFit"></image>
		</view>
	</view>
</template>

<script>
	export default {
		data() {
			return {
				imgSrc: []
			}
		},
		onLoad() {

		},
		methods: {
			getData() {
				uni.request({
					url: 'https://api-hmugo-web.itheima.net/api/public/v1/home/swiperdata',
					success: (res) => {
						console.log(res)
					}
				})
			},
			setStorage() {
				uni.setStorageSync('storage_key', 'hello')
			},
			getStorage() {
				const value = uni.getStorageSync('storage_key')
				console.log(value)
			},
			removeStorage() {
				uni.removeStorageSync('storage_key')
			},
			uploadPic() {
				uni.chooseImage({
					count: 6,
					sizeType: ["original", "compressed"],
					success: (res) => {
						console.log(res)
						this.imgSrc = res.tempFilePaths
					}
				})
			}
		}
	}
</script>

<style>
	.content {
		display: flex;
		flex-direction: column;
		align-items: center;
		justify-content: center;
	}

	.logo {
		height: 200rpx;
		width: 200rpx;
		margin-top: 200rpx;
		margin-left: auto;
		margin-right: auto;
		margin-bottom: 50rpx;
	}

	.text-area {
		display: flex;
		justify-content: center;
	}

	.title {
		font-size: 36rpx;
		color: #8f8f94;
	}
</style>
